.project {
  padding: 2em;
  margin: 0 auto;
  text-align: center;
  box-shadow: var(--shadow);
  transition: transform 0.2s linear;
  background-color: var(--clr-bg-alt);
  border-radius: 1em;
  cursor: pointer;
  position: relative;
}

.project:hover {
  transform: translateY(-7px);
}

.project h3 {
  color: var(--clr-primary);
  margin-bottom: 1em;
}

.project__description {
  margin: 1em 0;
}

.project__stack {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  margin: 1.2em 0;
}

.project__stack-item {
  background: var(--clr-bg);
  padding: 0.5em 1em;
  margin: 0.5em;
  border-radius: 5px;
  font-weight: 500;
  display: inline-block;
}

.project .link--icon {
  margin: 0 0.5em;
}

.project__preview {
  margin-top: 1em;
}

.project__preview-text {
  color: var(--clr-primary);
  font-weight: 500;
}

/* Styles pour le modal */
.project-modal {
  color: var(--clr-fg);
  max-width: 800px;
  margin: 0 auto;
  padding: 2em;
}

.project-modal h2 {
  color: var(--clr-primary);
  font-size: 2em;
  margin-bottom: 1em;
  text-align: center;
}

.project-modal h3 {
  color: var(--clr-fg-alt);
  margin: 1.5em 0 1em;
  font-size: 1.3em;
}

/* Styles pour l'article */
.article-content {
  text-align: left;
  line-height: 1.6;
}

.article-section {
  margin-bottom: 2em;
}

.article-section h3 {
  color: var(--clr-primary);
  font-size: 1.4em;
  margin: 1.5em 0 1em;
}

.article-section p {
  margin-bottom: 1em;
}

.article-section ul {
  list-style: disc;
  margin-left: 1.5em;
  margin-bottom: 1em;
}

.article-section li {
  margin: 0.5em 0;
}

.project-modal__tech {
  margin: 2em 0;
  text-align: center;
}

.project-modal__links {
  display: flex;
  justify-content: center;
  gap: 1em;
  margin-top: 2em;
}

.project-modal__links .btn {
  display: flex;
  align-items: center;
  gap: 0.5em;
}

.project-modal__links .btn svg {
  font-size: 1.2em;
}
